.footer {
    background-color: $secondary-color-darken;
    color: $text-color-5;
    font-size: 1.4rem;


    p {
        line-height: normal;
    }

    .site-brand {
        display: block;

        .logo {
            max-width: 220px;
            margin: auto;

            @include breakpoints(s-medium) {
                margin: 0;
                max-width: 260px;
            }
        }
    }

    .header {
        color: $text-color-4;
        font-size: 1.6rem;
        font-weight: bold;

        &--has-toggle-mobile {
            position: relative;
            cursor: pointer;
            display: flex;

            &:before {
                position: absolute;
                right: 0;
                top: 40%;
                content: '';
                width: 12px;
                height: 12px;
                border: 2px solid $text-color-4;
                border-radius: 2px;
                transform: translateY(-50%) rotate(45deg);
                border-top: 0;
                border-left: 0;
                transition: all .2s ease-in-out;

                @include breakpoints(s-medium) {
                    display: none;
                }

            }
        }
    }

    &__sml {
        display: flex;
        align-items: center;
        padding-left: 0;
        margin-bottom: 0;

        justify-content: center;

        @include breakpoints(s-medium) {
            justify-content: flex-start;
        }

        li {
            list-style-type: none;
            margin-bottom: 0;

            a {
                padding: 1rem;
                display: block;
                transition: all .3s ease-in-out;
                color: $text-color-5;

                .icon {
                    font-size: 1.8rem;
                    width: 20px;
                    height: 20px;
                    fill: $text-color-5;

                }

                &:hover {

                    .icon {

                        fill: $text-color-4;
                    }
                }
            }

            &:first-of-type {
                a {
                    padding-left: 0;
                }
            }
        }
    }

    &__menu {
        position: relative;
        height: 0;
        overflow: hidden;
        visibility: hidden;
        opacity: 0;
        transition: all .3s ease-in-out;
        padding-left: 0;

        @include breakpoints(s-medium) {
            height: auto;
            visibility: visible;
            opacity: 1;
        }

        li {
            list-style-type: none;
            margin-bottom: 0;
            a {
                padding: .6rem 0;
                display: block;
                transition: all .3s ease-in-out;
                color: $text-color-5;


                &:hover {
                    color: $text-color-4;
                }
            }
        }
    }

    .form, .form-search {
        position: relative;

        &:before {
            position: absolute;
            right: 15px;
            top: 45%;
            content: '';
            width: 12px;
            height: 12px;
            border: 2px solid $text-color-4;
            border-radius: 2px;
            transform: translateY(-50%) rotate(45deg);
            border-top: 0;
            border-left: 0;
            transition: all .2s ease-in-out;
            pointer-events: none;

        }

        select, .form__control {
            -webkit-appearance: none;
            appearance: none;
            background-color: $quarternary-color !important;
            @extend .btn;
            font-family: $font-family;
            padding: 1rem 2rem !important;
            text-align: left;
            width: auto;
            font-size: 1.6rem;
            color: #ccc !important;
            width: 100%;

            &::-webkit-input-placeholder {
                color: #ccc !important;
            }
            &:-moz-placeholder {
                color: #ccc !important;
            }
            &::-moz-placeholder {
                color: #ccc !important;
            }
            &:-ms-input-placeholder {
                color: #ccc !important;
            }
        }
    }

    .form-search {
        max-width: 260px;
        margin: auto;

        @include breakpoints(s-medium) {
            margin-left: 0;
        }

        &:before {
            display: none;
        }

        input[type=search]::-ms-clear {  display: none; width : 0; height: 0; }
        input[type=search]::-ms-reveal {  display: none; width : 0; height: 0;}
        
        /* clears the 'X' from Chrome */
        input[type="search"]::-webkit-search-decoration,
        input[type="search"]::-webkit-search-cancel-button,
        input[type="search"]::-webkit-search-results-button,
        input[type="search"]::-webkit-search-results-decoration { display: none;}
        

        .btn-search {
            background-color: transparent;
            border: 0;
            outline: none;
            position: absolute;
            top: 6px;
            right: 10px;
            cursor: pointer;

            .icon {
                font-size: 3rem;
                color: #ccc;
            }
        }

    }

    &__copy-right {
        border-top: 1px solid $text-color;
        padding-top: 3rem;
        margin-top: 3rem;
        text-align: center;

        @include breakpoints(medium) {
            text-align: left;
        }
    }

    &__payments {
        display: flex;
        justify-content: center;
        margin-bottom: 0;
        padding-left: 0;

        @include breakpoints(medium) {
            justify-content: flex-end;
        }

        li {
            margin: 0 .5rem;
            list-style-type: none;
            background-color: $color-white;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 2px;

            img {
                width: 35px;
                height: auto;
            }
        }
    }

    &__checkbox-toogle[type=checkbox]:checked {
        ~ .footer__menu {
            height: auto;
            visibility: visible;
            opacity: 1;
        }

        + .header--has-toggle-mobile {
            &:before {
                transform: translateY(-50%) rotate(-135deg);
                top: 50%;
            }
        }
    }

    .sub-menu{
        padding-left: 1.5rem !important;
    }

    .hp-menu {
        .sub-menu, #menu-item-5397 {
            display: none !important;
        }
    }

}

.landerMain #menu-help-support .sub-menu {
    display: none !important;
}